@import './exclude-me/fonts.scss',
'./exclude-me/variables.scss',
'./exclude-me/global.scss';

// ========================HEADER_TOP=============================
// ========================HEADER_TOP=============================

.header {
  background: $bg;
}

.header__top-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 0px;
}

.header__top-img-box {
  display: flex;
  align-items: center;

  a:first-child {
    margin-right: 24px;
  }
}

.header__top-nav {}

.header__top-nav-list {
  display: flex;
  align-items: center;

  li:not(:last-child) {
    margin-right: 40px;
  }
}

.header__top-nav-list-item {
  font-size: 14px;
  color: #FFFFFF;
  font-weight: 250;
}

.header__top-right-box {
  display: flex;
  align-items: center;
}

.header__top-socials-list {
  display: flex;
  align-items: center;
  margin-right: 40px;

  a:first-child {
    margin-right: 32px;
  }
}

.header__top-button {
  padding: 12px 20px;
  background: #FFFFFF;
  border-radius: 8px;
  font-size: 13px;
  color: #1A1A1A;
}

// ========================HEADER_BODY=============================
// ========================HEADER_BODY=============================

.header__body-inner {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding-bottom: 100px;
  margin-top: 100px;
}

.header__body-content {
  display: flex;
  flex-direction: column;
  align-items: start;
  margin-bottom: 144px;
}

.header__body-star-box {
  display: flex;
  align-items: center;

  img:not(:last-child) {
    margin-right: 23px;
  }
}

.header__body-title {
  font-weight: 700;
  font-size: 64px;
  max-width: 511px;
  margin: 40px 0;
}

.header__body-button {
  padding: 16px 24px;
  font-size: 18px;
}

.header__body-offers-box {
  display: flex;
  flex-direction: column;
}

.header__body-row {
  display: flex;
  align-items: center;

  &>div:not(:last-child) {
    margin-right: 32px;
  }
}

.header__body-row-title {
  font-weight: 500;
  font-size: 24px;
  margin-bottom: 48px;
}

.header__body-card {
  padding: 20px 24px;

  img {
    overflow: hidden;
    border-radius: 12px;
    margin-right: 28px;
  }
}

.header__body-card-text {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.header__body-card-title {
  font-size: 15px;
}

.header__body-card-price {
  font-weight: 500;
  font-size: 24px;
  margin: 12px 0;
  padding-left: 21px;
  position: relative;

  &::before {
    content: 'â‚½';
    font-weight: 500;
    font-size: 24px;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }
}

.header__body-card-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 13px;
}

// =============================ABOUT_US===================================
// =============================ABOUT_US===================================
.about {
  background: $bg;
}

.blur {
  background: $bg-blur;
}

.about__inner {
  padding: 209px 0 249px;
  display: flex;
  align-items: center;
}

.about__content {
  display: flex;
  flex-direction: column;
  align-items: start;
  margin-right: 64px;

  >*:not(:last-child) {
    margin-bottom: 40px;
  }
}

.about__title {
  font-weight: 700;
  font-size: 48px;
  max-width: 856px;
}

.about__desc {
  font-weight: 250;
  font-size: 18px;
}

.about__button {
  padding: 16px 24px;
  background: #FFFFFF;
  border-radius: 12px;
  font-size: 18px;
  color: #1A1A1A;
}

.about__right-wrapper {}

.about__card {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 28px 32px;
  max-width: 376px;

  img {
    margin-bottom: 28px;
    border-radius: 12px;
    overflow: hidden;
  }
}

.about__right-button {
  padding: 8px 12px;
  background: #3D324F;
  border-radius: 8px;
  font-size: 15px;
}

.about__card-title {
  margin: 12px 0;
  font-weight: 500;
  font-size: 24px;
}

.about__card-desc {
  font-size: 15px;
}

// =============================NUMBERS===================================
// =============================NUMBERS===================================

.numbers {
  background: $bg;
}

.numbers__inner {
  padding: 209px 0 284px;
}

.numbers__slider-wrapper {}

.numbers__slider {}

.numbers__slide {}

.swiper {
  height: 547px;

  &-slide {
    max-width: 416px;
    background: #465476;
    opacity: 0.75;
    border-radius: 12px;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;

    &-active,
    &-prev,
    &-next {
      opacity: 1;
      background: none;
    }
  }
}

.numbers__slider-card {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 28px 32px;
  overflow: hidden;


  img {
    margin-bottom: 28px;
    border-radius: 12px;
    overflow: hidden;
    width: 100%;
  }
}

.numbers__price {
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 15px;
}

.numbers__card-title {
  margin: 12px 0;
  font-weight: 500;
  font-size: 24px;
}

.numbers__card-desc {
  font-size: 15px;
}